"use client";
import { useState, useEffect, useRef } from "react";
import { useTranslations } from "next-intl";
import styles from "@/app/styles/componentStyles/headerNav.module.css";

export function HeaderNav() {
  const t = useTranslations("translation.nav");
  const [isOpen, setIsOpen] = useState(false);

  const menuRef = useRef<HTMLUListElement | null>(null);
  const burgerRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      console.log("event", menuRef.current);

      if (
        menuRef.current &&
        !menuRef.current.contains(event.target as Node) &&
        burgerRef.current &&
        !burgerRef.current.contains(event.target as Node)
      ) {
        setIsOpen(false); // –ó–∞–∫—Ä—ã–≤–∞–µ–º –º–µ–Ω—é
      }
    };

    // –î–æ–±–∞–≤–ª—è–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏–π
    document.addEventListener("mousedown", handleClickOutside);

    // –£–±–∏—Ä–∞–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –ø—Ä–∏ —Ä–∞–∑–º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  return (
    <nav className={styles.nav}>
      {/* –ë—É—Ä–≥–µ—Ä-–∏–∫–æ–Ω–∫–∞ */}
      <div
        className={styles.burger}
        onClick={() => setIsOpen(!isOpen)}
        ref={burgerRef} // –ü—Ä–∏–≤—è–∑—ã–≤–∞–µ–º –∫ –±—É—Ä–≥–µ—Ä-–∏–∫–æ–Ω–∫–µ
      >
        <span></span>
        <span></span>
        <span></span>
      </div>

      {/* –î–µ—Å–∫—Ç–æ–ø-–º–µ–Ω—é */}
      <ul className={styles.list}>
        {t
          .raw("items")
          .map(({ href, label, active }: { href: string; label: string; active?: boolean }) => (
            <li key={href} className={styles.item}>
              <a className={`${styles.link} ${active ? styles.active : ""}`} href={href}>
                {label}
              </a>
            </li>
          ))}
      </ul>

      {/* –ú–æ–±–∏–ª—å–Ω–æ–µ –º–µ–Ω—é */}
      {isOpen && (
        <ul className={styles.mobileMenu} ref={menuRef}>
          {" "}
          {t
            .raw("items")
            .map(({ href, label, active }: { href: string; label: string; active?: boolean }) => (
              <li key={href} className={styles.item}>
                <a
                  className={`${styles.link} ${active ? styles.active : ""}`}
                  href={href}
                  onClick={() => setIsOpen(false)} // –ó–∞–∫—Ä—ã–≤–∞–µ–º –º–µ–Ω—é –ø—Ä–∏ –∫–ª–∏–∫–µ
                >
                  {label}
                </a>
              </li>
            ))}
        </ul>
      )}
    </nav>
  );
}
